import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';

import InputDecorator from '../input-decorator';
import {
  Wrapper,
  Segment,
} from './styled';
import {DEFAULTS} from './constants';

const SegmentedControl = props => {
  const width = (props.segmentWidth || DEFAULTS.segmentWidth) * props.options.length;
  return (
    <InputDecorator
      label={props.label}
      additionalInfo={props.additionalInfo}
      isRequired={props.isRequired}
      isOptional={props.isOptional}
      helperText={props.helperText}
      margin={props.margin}
      fullHeight={props.fullHeight}
    >
      <Wrapper width={width}>
        {_.map(props.options, option => (
          <Segment
            key={option.value}
            height={props.height}
            isActive={option.value === props.value}
            onClick={() => {
              if (option.value !== props.value) {
                props.onChange(option.value);
              }
            }}
          >
            {option.label}
          </Segment>
        ))}
      </Wrapper>
    </InputDecorator>
  );
};

SegmentedControl.propTypes = {
  options: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.bool]),
    })
  ).isRequired,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.bool]),
  onChange: PropTypes.func,
  segmentWidth: PropTypes.number,
  height: PropTypes.string,
  margin: PropTypes.string,
  // Input decorator props
  label: PropTypes.string,
  additionalInfo: PropTypes.string,
  isRequired: PropTypes.bool,
  isOptional: PropTypes.bool,
  helperText: PropTypes.string,
  fullHeight: PropTypes.bool,
};

export default SegmentedControl;
