import React, {useEffect} from 'react';
import _ from 'lodash';
import {useQueryClient, useMutation, UseMutationResult} from 'react-query';
import toast from 'react-hot-toast';

// Logic
import {getRequestErrorMessage} from '../../../utils';
import {createRulesActionsOptions, createRuleTemplate} from '../utils';
import {useLocalState} from '../../../hooks';
import {loraFormState, rulesFormState} from '../state';
import {rulesApi, devicesRequests} from '../../../api';
import {selectRules} from '../selectors';
import {getUserId} from '../../../api/token';
import {CHIRP_STACK, CHIRP_STACK_COMMANDS} from '../constants';
import {getFormValues, isFormValid, getValidationErrorMessage} from '../../../utils/form-wizard';
import {Device, IdKeyedRules} from '../../../types';

// UI
import {FlexWrapper, HorizontalLine} from '../../../global/styled';
import {Heading} from '../../../global/components';
import TextField from '../../../components/text-field';
import SegmentedControl from '../../../components/segmented-control';
import Select from '../../../components/select';
import Button from '../../../components/button';
import Spinner from '../../../components/spinner';
import {
  LoraQueueContainer,
  LoraQueueCenterer,
  LoraQueueNoItemsNote,
  LoraQueueTable,
  LoraQueueTableHeaderItem,
  LoraQueueTableItem,
} from '../styled';

const RULES_ACTIONS = {
  turnOn: 'turnOn',
  turnOff: 'turnOff',
};

interface Props {
  deviceItem: Device;
  t: (key: string) => string;
}

interface GetQueueResponse {
  items: {
    devEUI: string;
    confirmed: boolean;
    fCnt: string;
    fPort: string;
    data: string;
  }[]
}

// NOTE: This tab is only for Chirpstack devices now.
// Support for other connectors needs to be implemented.

const LoRaControl = (props: Props) => {

  const loraForm = useLocalState(loraFormState);
  const ruleForm = useLocalState(rulesFormState);

  const queryClient = useQueryClient();

  const rulesQuery = rulesApi.useRulesQuery();

  const createRuleMutation = rulesApi.useCreateRule(queryClient);
  const removeRuleMutation = rulesApi.useRemoveRule(queryClient);
  const activateRuleMutation = rulesApi.useActivateRule();

  const sendLoraDownlinkMutation = useMutation(
    async () => devicesRequests.sendCommand({
      command: CHIRP_STACK_COMMANDS.loraAppServerQueueDownlink,
      integrationName: CHIRP_STACK,
      iotnodeId: props.deviceItem._id,
      data: getFormValues(loraForm.formInputs),
    }),
    {
      onError: error => {
        toast.error(getRequestErrorMessage(error));
      },
      onSuccess: () => {
        toast.success('Successfully sent downlink.');
        getQueueMutation.mutate();
        loraForm.resetForm();
      },
    }
  );

  const getQueueMutation = useMutation(
    async () => devicesRequests.sendCommand<GetQueueResponse>({
      command: CHIRP_STACK_COMMANDS.loraAppServerGetDeviceQueue,
      integrationName: CHIRP_STACK,
      iotnodeId: props.deviceItem._id,
    }),
    {
      onError: error => {
        toast.error(getRequestErrorMessage(error));
      }
    }
  );

  const flushQueueMutation = useMutation(
    async () => devicesRequests.sendCommand({
      command: CHIRP_STACK_COMMANDS.loraAppServerFlushQueue,
      integrationName: CHIRP_STACK,
      iotnodeId: props.deviceItem._id,
    }),
    {
      onError: error => {
        toast.error(getRequestErrorMessage(error));
      },
      onSuccess: () => {
        toast.success('Successfully flushed downlink queue.');
        getQueueMutation.mutate();
      },
    }
  );

  useEffect(() => {
    getQueueMutation.mutate();
  }, []);

  const setupRuleAction = () => {
    const template = createRuleTemplate({
      formInputs: ruleForm.formInputs,
      userId: getUserId(),
      deviceId: props.deviceItem._id,
      deviceItem: props.deviceItem,
    });
    createRuleMutation.mutate(template);
    ruleForm.resetForm();
  };

  const queueItems = getQueueMutation.data?.items;

  return (
    <>
      <div>
        <Heading
          heading='Send downlink'
          subHeading='Send downlink messages to the device'
          margin='0 0 30px 0'
        />
        <TextField
          label={'Data'}
          additionalInfo={'Hex data to be sent'}
          name={'data'}
          isRequired
          value={loraForm.formInputs.data.value as string}
          onChange={(evt: React.ChangeEvent<HTMLInputElement>) => {
            loraForm.setInputValue('data', evt.target.value);
            loraForm.showInputValidation('data');
          }}
          validationErrorMessage={
            loraForm.formInputs.data.value
              ? getValidationErrorMessage(loraForm.formInputs.data)
              : null
          }
          margin={'0 0 10px 0'}
        />
        <TextField
          label={'FPort'}
          additionalInfo={'Port field to be used. Must be between 1-223.'}
          name={'fPort'}
          isRequired
          value={loraForm.formInputs.fPort.value as string}
          onChange={(evt: React.ChangeEvent<HTMLInputElement>) => {
            loraForm.setInputValue('fPort', evt.target.value);
            loraForm.showInputValidation('fPort');
          }}
          validationErrorMessage={
            loraForm.formInputs.fPort.value
              ? getValidationErrorMessage(loraForm.formInputs.fPort)
              : null
          }
          margin={'0 0 10px 0'}
        />
        <TextField
          label={'Reference'}
          additionalInfo='Random reference (used on ack notification)'
          name={'reference'}
          onChange={(evt: React.ChangeEvent<HTMLInputElement>) => (
            loraForm.setInputValue('reference', evt.target.value)
          )}
          value={loraForm.formInputs.reference.value as string}
          margin={'0 0 10px 0'}
        />
        <SegmentedControl
          label={'Confirmed'}
          additionalInfo={'Whether an ACK is required from the node. Default is No.'}
          options={[
            {value: true, label: 'Yes'},
            {value: false, label: 'No'},
          ]}
          value={loraForm.formInputs.confirmed.value as string}
          onChange={(value: boolean) => loraForm.setInputValue('confirmed', value)}
          height={'30px'}
          margin={'0 0 30px 0'}
        />
        <Button
          label={'Send downlink'}
          disabled={!isFormValid(loraForm.formInputs)}
          onClick={() => sendLoraDownlinkMutation.mutate()}
          isLoading={sendLoraDownlinkMutation.isLoading}
          color={'green'}
          width={'130px'}
          margin={'0 0 30px 0'}
        />
      </div>

      <HorizontalLine />

      <div>
        <Heading
          heading='Downlink queue'
          subHeading='See the queued downlink messages for this device'
          margin='30px 0 30px 0'
        />
        <FlexWrapper>
          <Button
            label={'Refresh queue'}
            onClick={() => getQueueMutation.mutate()}
            disabled={getQueueMutation.isLoading}
            ghosted
            width={'110px'}
            color={'blue'}
            margin={'0 8px 0 0'}
          />
          <Button
            label={_.capitalize(props.t('labels.clearQueue'))}
            onClick={() => flushQueueMutation.mutate()}
            isLoading={flushQueueMutation.isLoading}
            ghosted
            color={'red'}
          />
        </FlexWrapper>

        <LoraQueueContainer>
          {getQueueMutation.isLoading && (
            <LoraQueueCenterer>
              <Spinner size={25} color={'#555'} />
            </LoraQueueCenterer>
          )}
          {_.isEmpty(queueItems) && !getQueueMutation.isLoading && (
            <LoraQueueCenterer>
              <LoraQueueNoItemsNote>Queue is empty</LoraQueueNoItemsNote>
            </LoraQueueCenterer>
          )}
          {!_.isEmpty(queueItems) && (
            <LoraQueueTable>
              <LoraQueueTableHeaderItem>Data</LoraQueueTableHeaderItem>
              <LoraQueueTableHeaderItem>fPort</LoraQueueTableHeaderItem>
              <LoraQueueTableHeaderItem>fCount</LoraQueueTableHeaderItem>
              <LoraQueueTableHeaderItem>Confirmed</LoraQueueTableHeaderItem>
              {_.map(queueItems, item => (
                <React.Fragment key={item.fCnt}>
                  <LoraQueueTableItem>{item.data}</LoraQueueTableItem>
                  <LoraQueueTableItem>{item.fPort}</LoraQueueTableItem>
                  <LoraQueueTableItem>{item.fCnt}</LoraQueueTableItem>
                  <LoraQueueTableItem>{item.confirmed ? 'Yes' : 'No'}</LoraQueueTableItem>
                </React.Fragment>
              ))}
            </LoraQueueTable>
          )}
        </LoraQueueContainer>

      </div>

      {/* @ts-ignore not yet typed */}
      <HorizontalLine margin={'30px 0 20px 0'} />

      <Heading
        heading='Custom downlink button'
        subHeading='Once a button has been created, go to the rule engine
        and edit your downlink'
        margin='30px 0 30px 0'
      />
      <FlexWrapper>
        <Select
          width={'250px'}
          placeholder={_.capitalize(props.t('placeholders.select'))}
          name={'rulesAction'}
          options={createRulesActionsOptions(props)}
          value={ruleForm.formInputs.rulesAction.value as string}
          onChange={(evt: React.ChangeEvent<HTMLInputElement>) => (
            ruleForm.setInputValue('rulesAction', evt.target.value)
          )}
        />
        <Button
          label={'Create'}
          disabled={!_.get(ruleForm.formInputs, 'rulesAction.value')}
          color={'green'}
          height={'38px'}
          ghosted
          width={'80px'}
          margin={'0 0 0 8px'}
          onClick={setupRuleAction}
        />
      </FlexWrapper>
      <Rule
        activateRuleMutation={activateRuleMutation}
        rules={rulesQuery.data}
        removeRuleMutation={removeRuleMutation}
        deviceItem={props.deviceItem}
      />

    </>
  );
};

type RemoveRule = UseMutationResult<string, unknown, {ruleId: string; actionId: string}, unknown>;

interface RuleProps {
  deviceItem: Device;
  rules?: IdKeyedRules;
  activateRuleMutation: UseMutationResult<void, unknown, string, unknown>;
  removeRuleMutation: RemoveRule;
}

const Rule = (props: RuleProps) => {
  const deviceRules = selectRules({rulesActions: props.rules, device: props.deviceItem});
  const rules = _.map(deviceRules, (rule, ruleId) => {
    const [event] = props.rules![ruleId].events!;
    return (
      <FlexWrapper key={ruleId}>
        <Button
          color={'green'}
          margin={'10px 0 10px'}
          height={'30px'}
          width={'150px'}
          content={RULES_ACTIONS[rule]}
          onClick={() => {
            props.activateRuleMutation.mutate(ruleId);
          }}
        />
        <Button
          color={'red'}
          ghosted
          margin={'10px'}
          height={'30px'}
          width={'30px'}
          content={'✖'}
          onClick={() => {
            props.removeRuleMutation.mutate({ruleId, actionId: event.action._id});
          }}
        />
      </FlexWrapper>
    );
  });
  return <>{rules}</>;
};

export default LoRaControl;
