import {Form} from '../../../../types';

const onChangeLocation = (
  setInputValue: Form['setInputValue'],
  showInputValidation: Form['showInputValidation'],
  evt: React.ChangeEvent<HTMLInputElement>
) => {
  const {target: {value, name}} = evt;
  setInputValue(name, value);
  showInputValidation(name);
  setInputValue('blueprint', '');
};

export {
  onChangeLocation,
};
