import React from 'react';
import {storiesOf} from '@storybook/react';

import ImageUploader, {BasicImageUploader} from './index';

storiesOf('Yggio/Image Uploader/Basic Image Uploader', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })

  .add('default', () => {
    const props = {
      // from top
      nameSpace: 'blueprints',
      // from yggio
      uploadImage: image => console.info('uploadImage', {image}),
      // from state
      imageItems: [],
      errorItems: [],
      addImageFiles: files => console.info('addImageFiles', {files}),
      addFileErrors: errors => console.info('addFileErrors', {errors}),
      removeImageItem: imageItem => console.info('removeImageItem', {imageItem}),
    };
    return (
      <BasicImageUploader {...props} />
    );
  });

storiesOf('Yggio/Image Uploader/Main Export', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })

