import React from 'react';
import _ from 'lodash';
import {Icon} from 'react-icons-kit';
import {ic_dehaze as SidebarIcon} from 'react-icons-kit/md/ic_dehaze';

import {
  HeadingBarContainer,
  HeadingBarLeftSection,
  HeadingBarMiddleSection,
  ToggleSidebarButton,
} from '../../../global/styled';

const HeadingBar = props => {
  return (
    <HeadingBarContainer>
      <HeadingBarLeftSection siblingWidth={props.siblingWidth}>
        <ToggleSidebarButton
          onClick={() => {
            if (props.isSidebarOpen) {
              props.closeSidebar();
            } else {
              props.openSidebar();
            }
          }}
        >
          <Icon icon={SidebarIcon} size={17} />
        </ToggleSidebarButton>
      </HeadingBarLeftSection>
      <HeadingBarMiddleSection>
        <p>{_.capitalize(props.t(`titles.${props.tabId}`))}</p>
      </HeadingBarMiddleSection>
    </HeadingBarContainer>
  );
};

export default HeadingBar;
