import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import dynamic from 'next/dynamic';

import {MAX_COORDINATE_LENGTH} from '../constants';
import {
  MapWrapper,
} from '../styled';
import {positionFormState} from '../state';
import {useLocalState} from '../../../hooks';
import {FlexWrapper} from '../../../global/styled';
import TextField from '../../../components/text-field';
import Button from '../../../components/button';
import InfoBox from '../../../components/info-box';

const Map = dynamic(async () => import('../../../components/map'), {ssr: false});

const PositionSelector = props => {
  const positionState = useLocalState(positionFormState);
  const entity = props.device.data;

  const getCoordinates = aEntity => {
    if (aEntity.latlng) return aEntity.latlng;

    return (
      (aEntity.lat && aEntity.lng)
        ? [aEntity.lat, aEntity.lng]
        : [
          positionState.formInputs.latitude.value,
          positionState.formInputs.longitude.value,
        ]
    );
  };

  useEffect(() => {
    const position = getCoordinates(entity);
    positionState.populateInputValues({
      latitude: position[0].toString(), longitude: position[1].toString()
    });
  }, []);

  const saveChanges = async () => {
    const newPosition = [
      Number(positionState.formInputs.latitude.value),
      Number(positionState.formInputs.longitude.value),
    ];

    await props.updateDevice({updates: {latlng: newPosition}});
    props.device.refetch();
  };

  const onPositionInputChanged = (key, value) => {
    const isValidNumber = !Number.isNaN(Number(value)) && value.length <= MAX_COORDINATE_LENGTH;
    if (value.length === 0 || isValidNumber) {
      positionState.setInputValue(key, value);
    }
  };

  const onCancelClick = () => {
    positionState.populateInputValues({
      latitude: entity.latlng[0].toString(), longitude: entity.latlng[1].toString()
    });
  };

  return (
    <>
      <h4>Set Position</h4>
      {
        !positionState.formInputs.latitude.value && !positionState.formInputs.longitude.value &&
          <InfoBox
            heading='Info'
            content="Click on the Map to set Device Position"
            type='info'
            width='305px'
          />
      }
      <FlexWrapper style={{width: '500px'}}>
        <TextField
          label={'Latitude'}
          margin={'10px 5px 10px'}
          width='200px'
          placeholder={'Latitude...'}
          name={'latitude'}
          value={positionState.formInputs.latitude.value}
          onChange={evt => onPositionInputChanged('latitude', evt.target.value)}
        />
        <TextField
          label={'Longitude'}
          margin={'10px 5px 10px'}
          width='200px'
          placeholder={'Longitude...'}
          name={'longitude'}
          value={positionState.formInputs.longitude.value}
          onChange={evt => onPositionInputChanged('longitude', evt.target.value)}
        />
      </FlexWrapper>
      <MapWrapper>
        <Map
          // @ts-ignore - seem to bug the ts-compiler for some unknown reason
          viewOnly
          showGetLocation
          router={props.router}
          height={'50vh'}
          width={'100%'}
          mapLayers={{
            devices: {
              _id: 'devices',
              items: [{
                latlng: [
                  Number(positionState.formInputs.latitude.value),
                  Number(positionState.formInputs.longitude.value)
                ],
                alone: true
              }],
              URI: 'devices',
              markerShape: 'round',
            }
          }}
          onClick={evt => positionState.populateInputValues({
            latitude: evt.latlng.lat.toString(), longitude: evt.latlng.lng.toString()
          })}
        />
      </MapWrapper>
      <FlexWrapper style={{width: '500px'}}>
        <Button
          color={'green'}
          content={'Set'}
          onClick={saveChanges}
        />
        <Button
          color={'grey'}
          margin={'0px 10px'}
          content={'Cancel'}
          onClick={onCancelClick}
          disabled={!entity.latlng}
        />
      </FlexWrapper>
    </>

  );
};

PositionSelector.propTypes = exact({
  device: PropTypes.object,
  t: PropTypes.func,
  updateDevice: PropTypes.func,
});

export default PositionSelector;
