import _ from 'lodash';
import {compose} from 'lodash/fp';

import {STATUS_TYPES} from '../constants';

const checkExpectedReportIntervalStatus = props => {
  if (props.device.expectedReportInterval) {
    const currentDate = Date.now();
    const reportedAtTime = new Date(props.device.reportedAt).getTime();
    const reportTime = (currentDate - props.device.expectedReportInterval);
    const hasNotReportedInTime = reportTime > reportedAtTime;
    if (hasNotReportedInTime) {
      props.items.push({
        text: props.t('deviceStatus.deviceDidNotReportInTime'),
        type: STATUS_TYPES.warning.name
      });
    }
  }

  return props;
};

const checkCalculatedDeviceStatus = props => {
  const isCalculatedDevice = _.find(props.calculations, calculation => {
    return _.get(calculation, 'destination.mongoId') === props.device._id;
  });
  if (isCalculatedDevice) {
    props.items.push({type: STATUS_TYPES.info.name, text: props.t('deviceStatus.calculated')});
  }

  return props;
};

const checkConnectorStatus = props => {
  const isConnector = !!props.device.downlinkQueue;
  const shouldHaveConnector = _.some([props.device.tag, props.device.devEui, _.get(props.device, 'nodeInfo.nodeId'), props.device.gatewayEui]) && !isConnector;
  if (props.device.connector && !_.isEmpty(props.device.connector)) {
    props.items.push({
      type: STATUS_TYPES.ok.name,
      text: `${props.t('deviceStatus.connectedTo')}: ${_.get(props.device, 'connector.name', 'unknown')}`
    });

    if (props.device.vid && props.device.pid) {
      const level = _.get(props.device, 'nodeInfo.commandClasses.0x0080.battery.level');
      if (level) {
        const levelText = level === '255' ? 'LOW' : `${level}%`;
        switch (true) {
          case (level <= 10):
            props.items.push({type: STATUS_TYPES.error.name, text: `${props.t('deviceStatus.battery')}:  ${levelText}`});
            break;
          case (level < 30 && level > 10):
            props.items.push({type: STATUS_TYPES.warning.name, text: `${props.t('deviceStatus.battery')}:  ${levelText}`});
            break;
          case (level <= 100 && level >= 30):
            props.items.push({type: STATUS_TYPES.ok.name, text: `${props.t('deviceStatus.battery')}:  ${levelText}`});
            break;
          case (level === '255'):
            props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.battery')});
            break;
          default:
            break;
        }
      }
    }
  } else if (shouldHaveConnector) {
    props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.noConnectorSet')});
  }

  if (!props.device.synchronizedAt && (shouldHaveConnector || isConnector)) {
    props.items.push({type: STATUS_TYPES.warning.name, text: props.t('deviceStatus.notSynchronizedWithIntegration')});
  }

  if (isConnector) {
    props.items.push({
      type: STATUS_TYPES.ok.name,
      text: `${props.t('deviceStatus.connectedToIntegration')}: ${props.device.downlinkQueue}`
    });
  }

  return props;
};

const checkRSSIStatus = props => {
  if (props.device.devEui) {
    const signalStrength = Number(props.device.rssi) + Number(props.device.snr || 0);
    if (Number.isNaN(signalStrength)) {
      props.items.push({type: STATUS_TYPES.warning.name, text: props.t('deviceStatus.signalUnknown')});
    } else {
      const strongSignal = signalStrength > -110;
      if (strongSignal) {
        props.items.push({type: STATUS_TYPES.ok.name, text: props.t('deviceStatus.signalStrong')});
      } else {
        props.items.push({type: STATUS_TYPES.warning.name, text: props.t('deviceStatus.signalWeak')});
      }
    }
  }

  return props;
};

const checkCoAPStatus = props => {
  if (props.device.resourceName) { // coap
    if (props.device.acknowledged) {
      props.items.push({type: STATUS_TYPES.ok.name, text: props.t('deviceStatus.acknowledgedByCoaP')});
    } else {
      props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.notAcknowledgedByCoaP')});
    }
  }

  return props;
};

const checkValueStatus = props => {
  const digital2 = _.get(props.device, 'value.digital2');
  if (_.eq(digital2, 0)) {
    props.items.push({type: STATUS_TYPES.ok.name, text: props.t('deviceStatus.waterLevelOK')});
  }

  if (_.eq(digital2, 1)) {
    props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.waterLevelCritical')});
  }

  const occupancy = _.get(props.device, 'value.occupancy');
  if (_.eq(occupancy, 0)) {
    props.items.push({type: STATUS_TYPES.ok.name, text: props.t('deviceStatus.notOccupied')});
  }

  if (_.eq(occupancy, 1)) {
    props.items.push({type: STATUS_TYPES.warning.name, text: props.t('deviceStatus.occupied')});
  }

  if (_.eq(occupancy, 2)) {
    props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.occupied')});
  }

  if (_.lte(occupancy, 75)) {
    props.items.push({type: STATUS_TYPES.ok.name, text: 'OK'});
  }

  const presence = _.get(props.device, 'value.presence.value');
  if (presence) {
    props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.occupied')});
  }

  const floodValue = _.get(props.device, 'value.flood.value');
  if (_.gt(floodValue, 75)) {
    props.items.push({type: STATUS_TYPES.error.name, text: props.t('deviceStatus.flooded')});
  }

  return props;
};

const getDeviceStatus = (device, calculations, t) => {
  const items = [];

  if (!device) {
    return [{
      text: t('deviceStatus.unidentifiedNodeType'),
      type: STATUS_TYPES.warning.name,
    }];
  }

  const result = compose(
    checkValueStatus,
    checkCoAPStatus,
    checkRSSIStatus,
    checkConnectorStatus,
    checkCalculatedDeviceStatus,
    checkExpectedReportIntervalStatus,
  )({device, calculations, t, items});

  const worstStatus = _.reduce(result.items, (acc, item) => {
    if (STATUS_TYPES[item.type].severity > acc.severity) {
      return STATUS_TYPES[item.type];
    }
    return acc;
  }, STATUS_TYPES.info);

  return {
    worstStatusType: worstStatus.name,
    items: result.items,
  };
};

export default getDeviceStatus;
