import React from 'react';
import useState from 'storybook-addon-state';

import ContextualParametersEditor from './index';
import {YggioFontDecorator} from '../../decorators';

const defaultValue = [
  {name: 'Placement', value: 'Floor'},
  {name: 'Installer', value: 'Markus'},
];

const main = {
  title: 'Yggio/Contextual Parameters Editor',
  component: ContextualParametersEditor,
  decorators: [YggioFontDecorator],
  argTypes: {

  }
};

const Template = args => {
  const [parameters, set] = useState('parameters', defaultValue);
  return (
    <ContextualParametersEditor
      onChange={parameters => set(parameters)}
      value={parameters}
      {...args}
    />
  );
};

const Primary = Template.bind({});
Primary.args = {
};

export default main;
export {
  Primary,
};
