import request from '../http-request';

import {
  REQUEST_METHODS,
  RESOURCE_TYPES,
} from '../../../constants';

const get = () => () => {
  const requestConfig = {
    method: REQUEST_METHODS.get,
    URI: RESOURCE_TYPES.requestBodySizeLimit,
  };
  return request(requestConfig, {isNonApiRoute: false});
};

export {
  get,
};
