import React from 'react';
import PropTypes from 'prop-types';
import {useQueryClient} from 'react-query';
import exact from 'prop-types-exact';
import _ from 'lodash';
import {
  FlexColWrapper,
  FlexMaxWidthCenterWrapper,
  FlexSpaceBetweenWrapper,
} from '../../../global/styled';
import TextField from '../../../components/text-field';
import Select from '../../../components/select';
import Button from '../../../components/button';
import {COLORS} from '../../../constants';
import {DeleteChannelButton, NoDataText} from '../styled';
import {useLocalState} from '../../../hooks';
import {channelState} from '../state';
import {channelsApi} from '../../../api';

const Channels = props => {
  const queryClient = useQueryClient();

  const {mutate: mutateCreateChannel} = channelsApi.useCreateChannel(queryClient);
  const {mutate: mutateRemoveChannel} = channelsApi.useRemoveChannel(queryClient);

  const channelForm = useLocalState(channelState);

  const createNewChannel = () => {
    const name = channelForm.formInputs.name.value;
    const protocol = channelForm.formInputs.protocol.value;
    const type = channelForm.formInputs.type.value;
    const recipient = channelForm.formInputs.recipient.value;
    const url = channelForm.formInputs.url.value;
    const connectionString = channelForm.formInputs.connectionString.value;
    const channel = {
      name,
      [protocol]: {
        type,
        recipient,
        url,
        connectionString
      },
      iotnode: props.deviceId,
    };
    mutateCreateChannel(channel);
    channelForm.resetForm();
  };

  return (
    <FlexColWrapper>
      {_.isEmpty(props.channels) && <NoDataText>{props.t('phrases.noChannelsAdded')}</NoDataText>}
      {!_.isEmpty(props.channels) && (
        <>
          <b>{_.capitalize(props.t('titles.topics'))}</b>
          {_.map(props.channels, channel => (
            <FlexSpaceBetweenWrapper key={channel._id} style={{margin: '2px', background: COLORS.greyLight}}>
              {_.get(channel, 'readableFormat')}
              <DeleteChannelButton onClick={() => mutateRemoveChannel(channel._id)}>
                ✖
              </DeleteChannelButton>
            </FlexSpaceBetweenWrapper>
          ))}
        </>
      )}

      <FlexColWrapper style={{width: '500px'}}>
        <h4>Create channel</h4>
        <TextField
          label={'Name'}
          margin={'10px 0 10px'}
          placeholder={'Name...'}
          name={'name'}
          value={_.get(channelForm.formInputs, 'name.value')}
          onChange={evt => channelForm.setInputValue('name', evt.target.value)}
        />


        <Select
          label={'Protocol'}
          margin={'10px 0 10px'}
          placeholder={_.capitalize(props.t('placeholders.select'))}
          name={'protocol'}
          options={[
            {value: 'mqtt', label: 'MQTT'},
            {value: 'http', label: 'HTTP'},
            {value: 'azureIotHub', label: 'Azure IoT Hub'},
          ]}
          value={_.get(channelForm.formInputs, 'protocol.value')}
          onChange={evt => channelForm.setInputValue('protocol', evt.target.value)}
        />

        {_.eq(_.get(channelForm.formInputs, 'protocol.value'), 'mqtt') && (
          <>
            <FlexMaxWidthCenterWrapper>
              <Select
                margin={'10px'}
                placeholder={'...type'}
                label={'type'}
                name={'type'}
                options={[
                  {value: 'keycloakUser', label: 'keycloakUser'},
                  {value: 'basicCredentialsSet', label: 'basicCredentialsSet'},
                ]}
                value={_.get(channelForm.formInputs, 'type.value')}
                onChange={evt => channelForm.setInputValue('type', evt.target.value)}
              />
            </FlexMaxWidthCenterWrapper>
            <FlexMaxWidthCenterWrapper>
              <TextField
                margin={'10px'}
                placeholder={'recipient...'}
                name={'recipient'}
                value={_.get(channelForm.formInputs, 'recipient.value')}
                onChange={evt => channelForm.setInputValue('recipient', evt.target.value)}
              />
            </FlexMaxWidthCenterWrapper>
          </>
        )}

        {_.eq(_.get(channelForm.formInputs, 'protocol.value'), 'http') && (
          <FlexMaxWidthCenterWrapper>
            <TextField
              margin={'10px'}
              placeholder={'URL...'}
              name={'url'}
              value={_.get(channelForm.formInputs, 'url.value')}
              onChange={evt => channelForm.setInputValue('url', evt.target.value)}
            />
          </FlexMaxWidthCenterWrapper>
        )}

        {_.eq(_.get(channelForm.formInputs, 'protocol.value'), 'azureIotHub') && (
          <FlexMaxWidthCenterWrapper>
            <TextField
              margin={'10px'}
              placeholder={'connection string...'}
              name={'connectionString'}
              value={_.get(channelForm.formInputs, 'connectionString.value')}
              onChange={evt => channelForm.setInputValue('connectionString', evt.target.value)}
            />
          </FlexMaxWidthCenterWrapper>
        )}

        <Button
          disabled={
            !_.get(channelForm.formInputs, 'name.value') ||
            !_.get(channelForm.formInputs, 'protocol.value') ||
            (_.eq(_.get(channelForm.formInputs, 'protocol.value'), 'http') && !(_.get(channelForm.formInputs, 'url.value'))) ||
            (_.eq(_.get(channelForm.formInputs, 'protocol.value'), 'azureIotHub') && !(_.get(channelForm.formInputs, 'connectionString.value')))
          }
          width={'200px'}
          onClick={createNewChannel}
          color={'green'}
          content={'Create channel'}
        />
      </FlexColWrapper>
    </FlexColWrapper>
  );
};

Channels.propTypes = exact({
  onChange: PropTypes.func,
  t: PropTypes.func,
  deviceId: PropTypes.string,
  channels: PropTypes.array,
  showAllInputValidations: PropTypes.func,
  populateInputValues: PropTypes.func,
  resetForm: PropTypes.func,
  setInputValue: PropTypes.func,
  showInputValidation: PropTypes.func,
  hideInputValidation: PropTypes.func,
  hideAllInputValidations: PropTypes.func,
  createNewChannel: PropTypes.func,
  formInputs: PropTypes.object,
  deviceItem: PropTypes.object,
  isPopulated: PropTypes.bool,
});

export default Channels;
