import React from 'react';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import _ from 'lodash';
import {Icon} from 'react-icons-kit';
import {ic_arrow_back as backIcon} from 'react-icons-kit/md/ic_arrow_back';

import {
  SidebarTopSection,
  BackButton,
  BackButtonText,
  SidebarInfoSection,
  SidebarDeviceNameHeading,
  SidebarDeviceName,
  SidebarInfoRow,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuItemLeftSection,
  SidebarMenuItemTitle,
  SidebarMenuItemIconWrapper,
  SidebarMenuItemNumberChip,
} from '../styled';
import {Modal} from '../../../components/modal';
import {StatusPopup} from '../../../yggio-managers/device-manager/sub-components';
import {SmallStatusChip, StatusChipIcon} from '../../../yggio-managers/device-manager/styled';
import {getTabItems} from '../utils';
import {Sidebar as SidebarContainer} from '../../../components/sidebar-components';
import {resolveDeviceType, formatTimeSinceLastReported} from '../../../utils';

const DetailsSidebar = props => {

  const tabItems = getTabItems(props.deviceItem, props.hasRecConnector);

  return (
    <SidebarContainer
      isSidebarOpen={props.isSidebarOpen}
      closeSidebar={props.closeSidebar}
      openSidebar={props.openSidebar}
      siblingWidth={900}
      isUsingNavbar
    >
      <SidebarTopSection>
        <BackButton onClick={() => props.router.push('/devices')}>
          <Icon icon={backIcon} size={14} />
          <BackButtonText>
            {_.capitalize(props.t('labels.backToDeviceList'))}
          </BackButtonText>
        </BackButton>
      </SidebarTopSection>

      <SidebarInfoSection>
        <SidebarDeviceNameHeading>
          {_.capitalize(props.t('common.device'))}:
        </SidebarDeviceNameHeading>
        <SidebarDeviceName>
          {_.get(props, 'deviceItem.name') || 'no-name'}
        </SidebarDeviceName>
        <SidebarInfoRow>
          <div>{_.capitalize(props.t('common.type'))}:</div>
          <div>{resolveDeviceType(props.deviceItem)}</div>
        </SidebarInfoRow>
        <SidebarInfoRow>
          <div>{_.capitalize(props.t('common.lastReported'))}:</div>
          <div>
            {formatTimeSinceLastReported(_.get(props, 'deviceItem.reportedAt'))}
          </div>
        </SidebarInfoRow>
        <SidebarInfoRow>
          <div>{_.capitalize(props.t('common.status'))}:</div>
          {
            _.size(props.status.items)
              ? (
                <SmallStatusChip
                  type={props.status.worstStatusType}
                  onClick={() => props.statusModal.open()}
                >
                  <StatusChipIcon type={props.status.worstStatusType} />
                  {_.size(props.status.items)}
                </SmallStatusChip>
              ) : <p>-</p>
          }
        </SidebarInfoRow>
        <Modal
          isOpen={props.statusModal.isOpen}
          close={props.statusModal.close}
          padding={'25px'}
        >
          <StatusPopup
            title={'Device status'}
            items={props.status.items}
            onClose={props.statusModal.close}
          />
        </Modal>
      </SidebarInfoSection>

      <SidebarMenu>
        {_.map(tabItems, item => (
          <SidebarMenuItem
            active={item.name === props.tabId}
            onClick={() => props.router.push(`/devices/${props.deviceId}/${item.name}`)}
            key={item.name}
          >
            <SidebarMenuItemLeftSection>
              <SidebarMenuItemIconWrapper
                topPosition={item.icon.topPosition}
                active={item.name === props.tabId}
              >
                <Icon icon={item.icon.file} size={item.icon.size} />
              </SidebarMenuItemIconWrapper>
              <SidebarMenuItemTitle active={item.name === props.tabId}>
                {_.capitalize(props.t(`titles.${item.name}`))}
              </SidebarMenuItemTitle>
            </SidebarMenuItemLeftSection>
            <SidebarMenuItemNumberChip
              hidden={_.isNil(props.numItems[item.name])}
              active={item.name === props.tabId}
            >
              {props.numItems[item.name]}
            </SidebarMenuItemNumberChip>
          </SidebarMenuItem>
        ))}
      </SidebarMenu>

    </SidebarContainer>
  );
};

DetailsSidebar.propTypes = exact({
  router: PropTypes.object.isRequired,
  deviceItem: PropTypes.object,
  handleDeviceRemoval: PropTypes.func,
  tabId: PropTypes.string,
  isSidebarOpen: PropTypes.bool,
  closeSidebar: PropTypes.func,
  openSidebar: PropTypes.func,
  numItems: PropTypes.object,
  siblingWidth: PropTypes.number,
  deviceId: PropTypes.string,
  t: PropTypes.func,
  status: PropTypes.object,
  statusModal: PropTypes.object,
  hasRecConnector: PropTypes.bool,
});

export default DetailsSidebar;
