import _ from 'lodash';

import {
  formatDistance,
  parseISO,
  isValid,
  millisecondsToHours,
  millisecondsToMinutes,
  millisecondsToSeconds,
  hoursToMilliseconds,
  minutesToMilliseconds,
} from 'date-fns';
import {
  RIGHT_TYPES,
  SPECIFIC_TAB_ITEMS,
  TAB_ITEMS,
  RULES_ACTIONS_OPTIONS,
  SPECIFIC_TAB_KEYS,
  REAL_ESTATE_CORE_TAB_ITEM,
} from './constants';
import {resolveDeviceType} from '../../utils';
import {DEVICE_TYPES, CALCULATIONS_TYPES} from '../../constants';

// TODO: This is wierd and needs a refactor
const getTabItems = (device, hasRecConnector) => {
  const general = _.values(TAB_ITEMS);

  const deviceType = resolveDeviceType(device);
  const types = deviceType.split(',');
  const currentType = _.pick(SPECIFIC_TAB_KEYS, types);
  const [specificType] = _.values(currentType);
  const specific = _.values(SPECIFIC_TAB_ITEMS[specificType] || []);

  const realEstateCore = hasRecConnector ? [REAL_ESTATE_CORE_TAB_ITEM] : [];

  const tabItems = _.concat(general, specific, realEstateCore);
  return tabItems;
};

const createRightsList = rights => {
  return _.reduce(RIGHT_TYPES, (acc, type) => {
    const right = _.find(rights, right => _.eq(right, type));
    acc[type] = !!right;
    return acc;
  }, {});
};

const createValidDate = date => {
  const validDate = parseISO(date);
  if (isValid(validDate)) {
    return formatDistance(new Date(), validDate);
  }
  return date;
};

const getNumDecimals = deviceStats => {
  let maxValue = -Infinity;
  _.forEach(deviceStats, device => {
    if (_.isNull(device.value)) return;
    if (Math.abs(device.value) > maxValue) maxValue = Math.abs(device.value);
  });
  if (maxValue === 0) return 2;
  // ex: ..., 1000 => 0, 100 => 0, 10 => 1, 1 => 2, 0.1 => 3, 0.01 => 4, ...
  return Math.max(0, 2 - Math.floor(Math.log10(Math.abs(maxValue))));
};

const convertMillisecondsToHoursMinutesSeconds = milliseconds => {
  const hours = millisecondsToHours(milliseconds);
  const hoursInMilliseconds = hoursToMilliseconds(hours);

  const minutes = millisecondsToMinutes(milliseconds - hoursInMilliseconds);
  const minutesInMilliseconds = minutesToMilliseconds(minutes);

  const seconds = millisecondsToSeconds(milliseconds - hoursInMilliseconds - minutesInMilliseconds);

  return {
    hours,
    minutes,
    seconds,
  };
};

const canBeSynchronized = device => {
  const hasConnector = !!device.connector;
  const isConnector = !!device.downlinkQueue;
  return hasConnector || isConnector;
};

const determineActionType = props => {
  const deviceType = resolveDeviceType(props.deviceItem);
  const deviceTypes = _.split(deviceType, ',');
  if (_.includes(deviceTypes, DEVICE_TYPES.lora)) {
    return 'LoraDeviceAction';
  }

  // TODO: In the future we will add Z-wave support
  // if (_.includes(deviceTypes, DEVICE_TYPES.zWave)) {
  //   return 'ZWaveDeviceAction';
  // }
};
const determineGroupName = props => {
  const deviceType = resolveDeviceType(props.deviceItem);
  const deviceTypes = _.split(deviceType, ',');
  if (_.includes(deviceTypes, DEVICE_TYPES.lora)) {
    return 'LoRa Enhet';
  }

  // TODO: In the future we will add Z-wave support
  // if (_.includes(deviceTypes, DEVICE_TYPES.zWave)) {
  //   return 'ZWave Enhet';
  // }
};

const determineMessageTemplate = props => {
  const deviceType = resolveDeviceType(props.deviceItem);
  const deviceTypes = _.split(deviceType, ',');
  if (_.includes(deviceTypes, DEVICE_TYPES.lora)) {
    return {
      confirmed: false,
      fPort: '123',
      data: '123',
      reference: '',
    };
  }

  // TODO: In the future we will add Z-wave support
  // if (_.includes(deviceTypes, DEVICE_TYPES.zWave)) {
  //   return {
  //     act: "set",
  //     cc: "bswitch",
  //     value: 0,
  //   };
  // }
};

const createRuleTemplate = props => {
  const {formInputs} = props;
  const template = {
    rule: {
      name: `${_.get(formInputs, 'rulesAction.value')}.${props.deviceId}.${props.deviceItem.name}`,
      triggers: [],
      owner: props.userId,
    },
    action: {
      name: `${_.get(formInputs, 'rulesAction.value')}.${props.deviceId}.${props.deviceItem.name}`,
      owner: props.userId,
      groupName: determineGroupName(props),
      message: determineMessageTemplate(props),
      actionType: determineActionType(props),
    },
  };
  return template;
};

const createRulesActionsOptions = props => {
  const options = _.filter(RULES_ACTIONS_OPTIONS, option => {
    if (!_.includes(_.values(props.rules), option.value)) {
      return option;
    }
  });
  return options;
};

const createSeekCalculationDevices = ({
  calculations,
  selectedCalculation,
  calculatedValues,
}) => {
  const currentCalculation = _.get(calculations, selectedCalculation);
  const calculationTypes = [
    CALCULATIONS_TYPES.monthlyDiffFromTotalEachNode,
    CALCULATIONS_TYPES.monthlySumPerEachNode,
    CALCULATIONS_TYPES.sumEachNode,
    CALCULATIONS_TYPES.averageEachNode,
    CALCULATIONS_TYPES.monthlyDiffFromTotal,
  ];
  const isValidCalculationType = _.includes(calculationTypes, _.get(currentCalculation, 'type'));
  if (isValidCalculationType) {
    const values = _.omit(calculatedValues, 'id');
    const idOmittedValues = _.omit(values.data, 'id');
    const deviceItems = _.flow([
      _.keys,
      _.flatten,
    ])(idOmittedValues);
    if (_.size(deviceItems)) {
      return deviceItems;
    }
  }
  return [];
};

export {
  getTabItems,
  createRightsList,
  createValidDate,
  getNumDecimals,
  convertMillisecondsToHoursMinutesSeconds,
  canBeSynchronized,
  createRuleTemplate,
  createRulesActionsOptions,
  createSeekCalculationDevices,
};
