const ACTION_TYPES = {
  open: 'open',
  close: 'close',
};

const defaultState = {
  isOpen: false,
};

const actions = {
  open: () => ({
    type: ACTION_TYPES.open,
  }),
  close: () => ({
    type: ACTION_TYPES.close,
  }),
};

const reducer = (state = defaultState, action) => {
  const {type} = action;
  switch (type) {

    case ACTION_TYPES.open: {
      return {
        isOpen: true,
      };
    }
    case ACTION_TYPES.close: {
      return {
        isOpen: false,
      };
    }

    default: {
      return state;
    }

  }
};

export default {
  reducer,
  actions,
};
