import React from 'react';
import _ from 'lodash';
import {compose} from 'lodash/fp';
import PropTypes from 'prop-types';
import exact from 'prop-types-exact';
import {Icon} from 'react-icons-kit';
import {ic_do_not_disturb as cancelIcon} from 'react-icons-kit/md/ic_do_not_disturb';
import {checkmark as checkIcon} from 'react-icons-kit/ionicons/checkmark';
import {ic_clear as removeIcon} from 'react-icons-kit/md/ic_clear';
import {ic_mode_edit as editIcon} from 'react-icons-kit/md/ic_mode_edit';

import {withState, withEvents} from '../../hocs';
import {isFormValid, getFormShape} from '../../utils/form-wizard';
import Spinner from '../../components/spinner';
import {generalState, formState} from './state';
import events from './events';
import {
  Container,
  Table,
  AddButton,
  SpinnerContainer,
  ParameterName,
  ParameterValue,
  TableButton,
  StyledInput,
  TableHeading,
} from './styled';

// BASIC

const BasicContextualParametersEditor = props => {
  return (
    <Container
      margin={props.margin}
      maxWidth={props.maxWidth}
    >
      {props.value.length > 0 && (
        <Table showTopBorder={!props.showTableHeading}>
          {props.showTableHeading && (
            <>
              <TableHeading>Name</TableHeading>
              <TableHeading>Value</TableHeading>
              <TableHeading />
              <TableHeading />
            </>
          )}
          {_.map(props.value, (parameter, index) => (
            <React.Fragment key={parameter.name}>
              {props.editingParameter !== index && (
                <TableRow {...props} parameter={parameter} index={index} />
              )}
              {props.editingParameter === index && (
                <TableRowEdit {...props} parameter={parameter} index={index} />
              )}
            </React.Fragment>
          ))}
        </Table>
      )}
      {(!props.isInAddMode && !props.isLoading) && (
        <AddButton
          onClick={() => props.enterAddMode()}
          disabled={props.editingParameter !== null}
        >
          + Add
        </AddButton>
      )}
      {(props.isInAddMode && !props.isLoading) && (
        <AddForm {...props} />
      )}
      {props.isLoading && (
        <SpinnerContainer>
          <Spinner size={18} color={'#555'} />
        </SpinnerContainer>
      )}
    </Container>
  );
};

const TableRow = props => {
  const disabled = props.editingParameter !== null || props.isInAddMode || props.isLoading;
  return (
    <>
      <ParameterName disabled={disabled}>
        {props.parameter.name}
      </ParameterName>
      <ParameterValue disabled={disabled}>
        {props.parameter.value}
      </ParameterValue>
      <TableButton
        onClick={() => !disabled && props.onEditStart(props.index)}
        disabled={disabled}
        hoverColor={'black'}
        title={'Edit'}
      >
        <Icon icon={editIcon} size={14} />
      </TableButton>
      <TableButton
        onClick={() => !disabled && props.onRemove(props.index)}
        disabled={disabled}
        hoverColor={'red'}
        title={'Delete'}
      >
        <Icon icon={removeIcon} size={17} />
      </TableButton>
    </>
  );
};

const TableRowEdit = props => {
  const disabled = !isFormValid(props.formInputs) || props.isLoading;
  const onKeyPressed = evt => {
    if (evt.key === 'Enter' && !disabled) {
      props.onEditSave(props.index);
    }
  };
  return (
    <>
      <ParameterName>
        <StyledInput
          type="text"
          onChange={evt => props.setInputValue('name', evt.target.value)}
          value={props.formInputs.name.value}
          autoFocus
          placeholder={'Name'}
          onKeyPress={onKeyPressed}
        />
      </ParameterName>
      <ParameterValue>
        <StyledInput
          type="text"
          onChange={evt => props.setInputValue('value', evt.target.value)}
          value={props.formInputs.value.value}
          placeholder={'Value'}
          onKeyPress={onKeyPressed}
        />
      </ParameterValue>
      <TableButton
        onClick={() => props.onCancelEditing()}
        hoverColor={'black'}
        title={'Cancel'}
      >
        <Icon icon={cancelIcon} size={14} />
      </TableButton>
      <TableButton
        onClick={() => !disabled && props.onEditSave(props.index)}
        hoverColor={'green'}
        title={'Save'}
        disabled={disabled}
      >
        <Icon icon={checkIcon} size={15} />
      </TableButton>
    </>
  );
};

const AddForm = props => {
  const disabled = !isFormValid(props.formInputs) || props.isLoading;
  const onKeyPressed = evt => {
    if (evt.key === 'Enter' && !disabled) {
      events.onAddSave(props);
    }
  };
  return (
    <Table showTopBorder>
      <ParameterName>
        <StyledInput
          type="text"
          onChange={evt => props.setInputValue('name', evt.target.value)}
          value={props.formInputs.name.value}
          autoFocus
          placeholder={'Name'}
          onKeyPress={onKeyPressed}
        />
      </ParameterName>
      <ParameterValue>
        <StyledInput
          type="text"
          onChange={evt => props.setInputValue('value', evt.target.value)}
          value={props.formInputs.value.value}
          placeholder={'Value'}
          onKeyPress={onKeyPressed}
        />
      </ParameterValue>
      <TableButton
        onClick={() => props.onCancelAdding()}
        hoverColor={'black'}
        title={'Cancel'}
      >
        <Icon icon={cancelIcon} size={14} />
      </TableButton>
      <TableButton
        onClick={() => !disabled && props.onAddSave()}
        hoverColor={'green'}
        title={'Save'}
        disabled={disabled}
      >
        <Icon icon={checkIcon} size={14} />
      </TableButton>
    </Table>
  );
};

BasicContextualParametersEditor.propTypes = exact({
  // from top
  onChange: PropTypes.func.isRequired,
  value: PropTypes.array.isRequired,
  showRemovePromt: PropTypes.bool,
  showTableHeading: PropTypes.bool,
  isLoading: PropTypes.bool,
  margin: PropTypes.string,
  maxWidth: PropTypes.string,
  // from state
  enterAddMode: PropTypes.func,
  exitAddMode: PropTypes.func,
  startEditingParameter: PropTypes.func,
  cancelEditing: PropTypes.func,
  isInAddMode: PropTypes.bool,
  editingParameter: PropTypes.number,
  // from events
  onAddSave: PropTypes.func,
  onEditSave: PropTypes.func,
  onRemove: PropTypes.func,
  onEditStart: PropTypes.func,
  onCancelEditing: PropTypes.func,
  onCancelAdding: PropTypes.func,
  // from form state
  ...getFormShape({
    name: PropTypes.string,
    value: PropTypes.string,
  }),
});

// RAW

const ContextualParametersEditor = compose(
  withState(generalState),
  withState(formState),
  withEvents(events),
)(BasicContextualParametersEditor);

ContextualParametersEditor.propTypes = exact({
  onChange: PropTypes.func.isRequired,
  value: PropTypes.array.isRequired,
  showRemovePromt: PropTypes.bool,
  showTableHeading: PropTypes.bool,
  isLoading: PropTypes.bool,
  margin: PropTypes.string,
  maxWidth: PropTypes.string,
});

export default ContextualParametersEditor;
