import React from 'react';
import {Toaster} from 'react-hot-toast';

const parameters = {
  options: {
    showPanel: true,
    panelPosition: 'right',
  },
};

const ToasterDecorator = (storyFn) => (
  <>
    <Toaster position={'bottom-left'} />
    {storyFn()}
  </>
);

const decorators = [ToasterDecorator];

export {
  parameters,
  decorators,
};
