import request from '../http-request';

import {
  REQUEST_METHODS,
  RESOURCE_TYPES,
} from '../../../constants';

const fetch = token => () => request({
  token,
  method: REQUEST_METHODS.get,
  URI: `${RESOURCE_TYPES.coreApps}`,
});

const get = token => appId => request({
  token,
  method: REQUEST_METHODS.get,
  URI: `${RESOURCE_TYPES.coreApps}/${appId}`,
  params: {appId}
});

export {
  fetch,
  get,
};
