import React from 'react';
import _ from 'lodash';
import {useQueryClient} from 'react-query';
import {confirmAlert} from 'react-confirm-alert';
import {compose} from 'lodash/fp';
import getDeviceStatus from '../../utils/get-device-status';
import {
  selectNumItems,
  selectCalculations,
} from './selectors';
import {modalState} from '../../components/modal';
import {
  AccessRights,
  Specifications,
  LoRaControl,
  Channels,
  Box2Downlink,
  GeneralInfo,
  ContextualParameters,
  Data,
  ReportInterval,
  Charts,
  Tools,
  PositionSelector
} from './sub-components';
import RecDeviceEditor from '../rec-device-editor';
import {Calculations} from './containers';
import {
  TAB_ITEMS,
  SIDEBAR_SIBLING_WIDTH,
  LORA_TAB_ITEMS,
  BOX2_TAB_ITEMS,
  REAL_ESTATE_CORE_TAB_ITEM,
} from './constants';
import withLanguage from '../../hocs/with-language';
import DetailsSidebar from './sub-components/sidebar';
import HeadingBar from './sub-components/heading-bar';
import ContainerBox from '../../components/container-box';
import Spinner from '../../components/spinner';
import {
  SidebarParent,
  SidebarSibling,
  sidebarState,
} from '../../components/sidebar-components';
import {
  MainContentWrapper,
  MainContentContainer,
} from '../../yggio-managers/device-manager/styled';
import {LoadingView} from './styled';
import {COLORS} from '../../constants';
import {
  devicesApi,
  channelsApi,
  calculationsApi,
} from '../../api';
import {useLocalState} from '../../hooks';

const BasicDeviceDetailPane = props => {

  //
  //
  // HOOKS
  //
  //

  const queryClient = useQueryClient();

  const deviceQuery = devicesApi.useDeviceQuery({deviceId: props.deviceId});

  const recConnectorDevicesQuery = devicesApi.useConnectorDevicesQuery('RealEstateCore');

  const channels = channelsApi.useChannelsQuery(props.deviceId);
  const calculations = calculationsApi.useCalculationsQuery();
  const calculationsItems = selectCalculations({
    calculations: calculations.data,
    currentDevice: deviceQuery,
  });
  const updateDeviceMutation = devicesApi.useUpdateDevice(queryClient);
  const {mutate: mutateRemoveDevice} = devicesApi.useRemoveDevice(queryClient);

  const sidebarForm = useLocalState(sidebarState);
  const modalForm = useLocalState(modalState);

  //
  //
  // SELECTORS
  //
  //

  const numItems = selectNumItems({
    calculations: calculationsItems,
    channels: channels.data,
    contextualParameters: deviceQuery.data?.contextMap,
  });
  const status = getDeviceStatus(
    deviceQuery?.data,
    calculations?.data,
    props.t,
  );

  //
  //
  // EVENT HANDLERS
  //
  //

  const removeDevice = () => {
    const buttons = [
      {
        label: _.capitalize(props.t('common.yes')),
        onClick: () => {
          mutateRemoveDevice({deviceId: props.deviceId});
          props.router.push('/devices');
        }
      },
      {
        label: _.capitalize(props.t('common.no')),
        onClick: () => { },
      }
    ];
    confirmAlert({
      title: _.capitalize(props.t('labels.confirmation')),
      message: props.t('phrases.deleteDeviceVerification'),
      buttons,
    });
  };

  //
  //
  // RENDERING
  //
  //

  if (deviceQuery.isError) {
    return <div>Failed to get device</div>;
  }

  if (deviceQuery.isLoading) {
    return (
      <LoadingView>
        <Spinner color={COLORS.greenRacing} size={30} margin={'0 0 5px 0'} />
        {props.t('phrases.fetchingDevice')}...
      </LoadingView>
    );
  }

  return (
    <SidebarParent>
      <DetailsSidebar
        router={props.router}
        deviceItem={deviceQuery.data}
        deviceId={props.deviceId}
        tabId={props.tabId}
        isSidebarOpen={sidebarForm.isSidebarOpen}
        closeSidebar={sidebarForm.closeSidebar}
        openSidebar={sidebarForm.openSidebar}
        status={status}
        statusModal={modalForm}
        numItems={numItems}
        siblingWidth={SIDEBAR_SIBLING_WIDTH}
        hasRecConnector={!_.isEmpty(recConnectorDevicesQuery.data)}
        t={props.t}
      />
      <SidebarSibling isSidebarOpen={sidebarForm.isSidebarOpen}>
        <MainContentWrapper>
          <MainContentContainer maxWidth={SIDEBAR_SIBLING_WIDTH}>
            <HeadingBar
              closeSidebar={sidebarForm.closeSidebar}
              openSidebar={sidebarForm.openSidebar}
              isSidebarOpen={sidebarForm.isSidebarOpen}
              tabId={props.tabId}
              siblingWidth={SIDEBAR_SIBLING_WIDTH}
              t={props.t}
            />
            <ContainerBox padding={'30px'}>
              {{
                [TAB_ITEMS.generalInfo.name]: (
                  <GeneralInfo
                    updateDeviceMutation={updateDeviceMutation}
                    t={props.t}
                    deviceQuery={deviceQuery}
                    handleDeviceRemoval={removeDevice}
                  />
                ),
                [TAB_ITEMS.specifications.name]: (
                  <Specifications
                    device={deviceQuery.data}
                    t={props.t}
                  />
                ),
                [TAB_ITEMS.position.name]: (
                  <PositionSelector
                    updateDevice={updateDeviceMutation}
                    t={props.t}
                    device={deviceQuery}
                  />
                ),
                [TAB_ITEMS.channels.name]: (
                  <Channels
                    channels={channels?.data}
                    deviceItem={deviceQuery.data}
                    t={props.t}
                    deviceId={props.deviceId}
                  />
                ),
                [TAB_ITEMS.calculations.name]: (
                  <Calculations
                    device={deviceQuery.data}
                    router={props.router}
                    queryClient={queryClient}
                    goToDeviceDetail={props.goToDeviceDetail}
                    calculations={calculations?.data}
                    calculationsItems={calculationsItems}
                    handleCalculationRemoval={props.handleCalculationRemoval}
                    updateDeviceMutation={updateDeviceMutation}
                    deleteCalculation={props.deleteCalculation}
                    t={props.t}
                  />
                ),
                // Removed until translator tab is
                // [TAB_ITEMS.translators.name]: (
                //   <Translators
                //     updateDeviceMutation={updateDeviceMutation}
                //     t={props.t}
                //     deviceItem={deviceQuery.data}
                //   />
                // ),
                [TAB_ITEMS.accessrights.name]: (
                  <AccessRights
                    t={props.t}
                    existingUsers={props.existingUsers}
                    deviceId={props.deviceId}
                    device={deviceQuery.data}
                  />
                ),
                [LORA_TAB_ITEMS.loraControl.name]: (
                  <LoRaControl
                    deviceItem={deviceQuery.data}
                    t={props.t}
                  />
                ),
                [TAB_ITEMS.data.name]: (
                  <Data
                    deviceItem={deviceQuery.data}
                    t={props.t}
                  />
                ),
                [BOX2_TAB_ITEMS.downlink.name]: (
                  <Box2Downlink
                    updateDeviceMutation={updateDeviceMutation}
                    deviceId={props.deviceId}
                    t={props.t}
                  />
                ),
                [TAB_ITEMS.contextualParameters.name]: (
                  <ContextualParameters
                    contextMap={props.contextMap}
                    t={props.t}
                    updateDeviceMutation={updateDeviceMutation}
                    deviceId={props.deviceId}
                    deviceItem={deviceQuery.data}
                  />
                ),
                [TAB_ITEMS.charts.name]: (
                  <Charts
                    deviceId={props.deviceId}
                    router={props.router}
                  />
                ),
                [TAB_ITEMS.reportInterval.name]: (
                  <ReportInterval
                    deviceItem={deviceQuery.data}
                  />
                ),
                [TAB_ITEMS.tools.name]: (
                  <Tools
                    deviceItem={deviceQuery.data}
                  />
                ),
                [REAL_ESTATE_CORE_TAB_ITEM.name]: (
                  <RecDeviceEditor
                    deviceId={props.deviceId}
                    connectors={recConnectorDevicesQuery.data}
                  />
                ),
              }[props.tabId]}
            </ContainerBox>
          </MainContentContainer>
        </MainContentWrapper>
      </SidebarSibling>
    </SidebarParent>
  );
};

export default compose(
  withLanguage(),
)(BasicDeviceDetailPane);
