import request from '../http-request';
import {
  REQUEST_METHODS,
  RESOURCE_TYPES,
} from '../../../constants';

const fetch = token => deviceModelName => request({
  token,
  method: REQUEST_METHODS.get,
  URI: RESOURCE_TYPES.translators,
  params: {
    deviceModelName,
  }
});

export {
  fetch,
};
