import React from 'react';
import _ from 'lodash';
import {FlexColWrapper} from '../../../global/styled';
import TextField from '../../../components/text-field';
import Button from '../../../components/button';
import {useLocalState} from '../../../hooks';
import {box2FormState} from '../state';

const Box2Downlink = props => {
  const box2Form = useLocalState(box2FormState);

  const sendBox2Downlink = () => {
    const updates = {
      value: {
        value: Number(box2Form.formInputs.box2downlink.value)
      },
    };
    props.updateDeviceMutation.mutate({updates, deviceId: props.deviceId});
    box2Form.resetForm();
  };

  return (
    <FlexColWrapper
      style={{width: '30%'}}
    >
      <TextField
        name={'box2downlink'}
        placeholder={'Value... (Number)'}
        onChange={evt => box2Form.setInputValue('box2downlink', evt.target.value)}
        value={_.get(box2Form.formInputs, 'box2downlink.value')}
        margin={'0 0 10px 0'}
      />
      <Button
        disabled={!_.get(box2Form.formInputs, 'box2downlink.value')}
        ghosted
        color={'green'}
        onClick={sendBox2Downlink}
        content={_.capitalize(props.t('labels.send'))}
        width={'300px'}
      />
    </FlexColWrapper>
  );
};

export default Box2Downlink;
