const ACTION_TYPES = {
  enterAddMode: 'enterAddMode',
  exitAddMode: 'exitAddMode',
  startEditingParameter: 'startEditingParameter',
  cancelEditing: 'cancelEditing',
};

const actions = {
  enterAddMode: () => ({
    type: ACTION_TYPES.enterAddMode,
  }),
  exitAddMode: () => ({
    type: ACTION_TYPES.exitAddMode,
  }),
  startEditingParameter: index => ({
    type: ACTION_TYPES.startEditingParameter,
    payload: {index},
  }),
  cancelEditing: () => ({
    type: ACTION_TYPES.cancelEditing,
  }),
};

const defaultState = {
  isInAddMode: false,
  editingParameter: null,
};

const reducer = (state = defaultState, action) => {
  const {type, payload} = action;
  switch (type) {

    case ACTION_TYPES.enterAddMode: {
      return {
        ...state,
        isInAddMode: true,
      };
    }

    case ACTION_TYPES.exitAddMode: {
      return {
        ...state,
        isInAddMode: false,
      };
    }

    case ACTION_TYPES.startEditingParameter: {
      const {index} = payload;
      return {
        ...state,
        editingParameter: index,
      };
    }

    case ACTION_TYPES.cancelEditing: {
      return {
        ...state,
        editingParameter: null,
      };
    }

    default: {
      return state;
    }

  }
};

export default {
  actions,
  reducer,
};
