// containers.js

import React from 'react';
import PropTypes from 'prop-types';

import {
  MemberItemWrapper,
} from './styled';

const MemberItem = props => (
  <MemberItemWrapper>
    <p>{`${props.index + 1}.  ${props.member.username}`}</p>
  </MemberItemWrapper>
);
MemberItem.propTypes = {
  member: PropTypes.object.isRequired,
};

export {
  MemberItem,
};
