const ACTION_TYPES = {
  setPage: 'setPage',
};

const actions = {
  setPage: page => ({
    type: ACTION_TYPES.setPage,
    payload: {page},
  }),
};

const defaultState = {
  page: 'default',
};

const reducer = (state = defaultState, action) => {
  const {type, payload} = action;
  switch (type) {

    case ACTION_TYPES.setPage: {
      const {page} = payload;
      return {
        ...state,
        page,
      };
    }

    default: {
      return state;
    }

  }
};

export default {
  actions,
  reducer,
};
