import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import {makeStyles} from '@material-ui/core/styles';
import TreeView from '@material-ui/lab/TreeView';
import TreeItem from '@material-ui/lab/TreeItem';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';

import styles from './styles';

const DataViewer = props => {

  const useStyles = makeStyles(styles(props));

  const classes = useStyles();

  let i = 0;

  const printValue = value => {
    if (_.isNull(value)) {
      return <p className={classes.valueFalsy}>null</p>;
    }
    if (_.isUndefined(value)) {
      return <p className={classes.valueFalsy}>undefined</p>;
    }
    if (_.isString(value) && value.length === 0) {
      return <p className={classes.valueFalsy}>empty string</p>;
    }
    if (_.isString(value)) {
      return <p className={classes.valueString}>{value}</p>;
    }
    if (_.isBoolean(value)) {
      return <p className={classes.valueBoolean}>{value.toString()}</p>;
    }
    if (_.isNumber(value)) {
      return <p className={classes.valueNumber}>{value.toString()}</p>;
    }
    return <p className={classes.valueDefault}>{value}</p>;
  };

  const Label = ({name, value}) => (
    <div className={classes.labelRoot}>
      {!_.isNull(name) && (
        <p className={classes.labelName}>{name}{!_.isObjectLike(value) && ':'}</p>
      )}
      {!_.isObjectLike(value) && (
        printValue(value)
      )}
    </div>
  );

  const renderTreeItem = (key, value) => {
    i++;
    return (
      <TreeItem key={i.toString()} nodeId={i.toString()} label={<Label name={key} value={value} />}>
        {
          _.isObjectLike(value)
            ? _.map(value, (nextValue, nextKey) => renderTreeItem(nextKey, nextValue))
            : null
        }
      </TreeItem>
    );
  };

  return (
    <TreeView
      className={classes.root}
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpandIcon={<ChevronRightIcon />}
    >
      {
        _.isObjectLike(props.data)
          ? _.map(props.data, (value, key) => renderTreeItem(key, value))
          : renderTreeItem(null, props.data)
      }
    </TreeView>
  );
};

DataViewer.propTypes = {
  data: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
    PropTypes.array,
  ]),
  width: PropTypes.string,
  margin: PropTypes.string,
};

export default DataViewer;
