import _ from 'lodash';
import {confirmAlert} from 'react-confirm-alert';

const onAddSave = props => () => {
  const name = props.formInputs.name.value;
  if (_.some(props.value, ['name', name])) {
    showNameConflicPromt(name);
  } else {
    const modifiedParameters = [...props.value];
    modifiedParameters.push({
      name,
      value: props.formInputs.value.value,
    });
    props.onChange(modifiedParameters);
    props.exitAddMode();
    props.resetForm();
  }
};

const onEditSave = props => index => {
  const name = props.formInputs.name.value;
  const parametersExcludingCurrent = [...props.value];
  _.pullAt(parametersExcludingCurrent, index);
  if (_.some(parametersExcludingCurrent, ['name', name])) {
    showNameConflicPromt(name);
  } else {
    const modifiedParameters = [...props.value];
    modifiedParameters[index] = {
      name: props.formInputs.name.value,
      value: props.formInputs.value.value
    };
    if (!_.isEqual(props.value[index], modifiedParameters[index])) {
      props.onChange(modifiedParameters);
    }
    props.cancelEditing();
    props.resetForm();
  }
};

const onRemove = props => index => {
  const performRemove = () => {
    const modifiedParameters = [...props.value];
    _.pullAt(modifiedParameters, [index]);
    props.onChange(modifiedParameters);
  };
  if (props.showRemovePromt) {
    showRemovePromt(props.value[index].name, performRemove);
  } else {
    performRemove();
  }
};

const onCancelEditing = props => () => {
  props.cancelEditing();
  props.resetForm();
};

const onCancelAdding = props => () => {
  props.exitAddMode();
  props.resetForm();
};

const onEditStart = props => index => {
  const {name, value} = props.value[index];
  props.setInputValue('name', name);
  props.setInputValue('value', value);
  props.startEditingParameter(index);
};

const showNameConflicPromt = name => {
  confirmAlert({
    title: 'Name conflict',
    message: `There already exists a parameter with name: "${name}"`,
    buttons: [{label: 'Ok'}]
  });
};

const showRemovePromt = (name, performRemove) => {
  const buttons = [
    {
      label: 'Yes',
      onClick: () => performRemove(),
    },
    {
      label: 'No',
      onClick: () => {}
    }
  ];
  confirmAlert({
    title: 'Remove contextual parameter?',
    message: `Are you sure you want the remove: "${name}"?`,
    buttons,
  });
};

export default {
  onAddSave,
  onEditSave,
  onRemove,
  onEditStart,
  onCancelEditing,
  onCancelAdding,
};
