// version.js

import request from '../http-request';

import {
  REQUEST_METHODS,
  RESOURCE_TYPES,
} from '../../../constants';

const getVersion = () => () => {
  const requestConfig = {
    method: REQUEST_METHODS.get,
    URI: RESOURCE_TYPES.version,
  };
  return request(requestConfig, {isNonApiRoute: true});
};

export {
  getVersion,
};
